function sliderSuppliers() {
    var swiper = new Swiper('.suppliers .mySwiper', {
        slidesPerView: "auto",
        spaceBetween: 40,
        breakpoints: {
            320: {
                spaceBetween: 20,
            },
            993: {
                spaceBetween: 40,
            }
        }
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        slidesPerView: 3,
        spaceBetween: 25,
        freeMode: true,
        watchSlidesProgress: true,
        direction: "vertical",
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 25,
        slidesPerView: 1,
        thumbs: {
            swiper: swiper,
        },
        pagination: {
            el: ".gallery .swiper-pagination",
            clickable: true,
        },
    });
}

function sliderProducts() {
    var swiper = new Swiper('.products .mySwiper', {
        slidesPerView: "4",
        spaceBetween: 40,
        navigation: {
            nextEl: ".products .swiper-button-next",
            prevEl: ".products .swiper-button-prev",
        },
        breakpoints: {
            320: {
                slidesPerView: "1.1",
                spaceBetween: 20,
            },
            451: {
                slidesPerView: "2",
                spaceBetween: 20,
            },
            769: {
                slidesPerView: "3",
                spaceBetween: 20,
            },
            993: {
                slidesPerView: "4",
                spaceBetween: 20,
            },
            1401: {
                slidesPerView: "4",
                spaceBetween: 40,
            }
        }
    })
}

$(document).ready(function() {
    sliderSuppliers();
    gallery();
    sliderProducts();

    // Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-bottom').toggleClass('header-bottom--active')
        $('body').toggleClass('fixed-body')
    })

    // input
    function inputPlaceholder() {
        if($(document).width() < 1200 ? true : false){
            $('.header__search input').attr('placeholder', 'Найти');
        }
    }
    inputPlaceholder();

    // Favorite
    $('.favorite').click(function () {
        $(this).toggleClass('favorite--active')
    })

    // Accordion
    $(".item-show").click((function(){
        $(this).toggleClass("item-show--active"),
        $(this).siblings(".item-hidden").slideToggle()
    }));

    $('.orders__btn-hide').click(function () {
        $(this).parents('.item-hidden').slideToggle()
    })

    // Show-all
    $('.catalog__list-btn').click(function () {
        $(this).parents('.catalog__list-mob').addClass('catalog__list-mob--active')
        $(this).remove()
    })

    // Calc
    function cartCalc(){
        $('.cartcalc .ccalc-minus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            if(a>1){
                let b = +a-1;
                $(this).closest('.cartcalc').find('input').val(b);
            }else{
                $(this).closest('.cartcalc').find('input').val(a);
            }
        });
        $('.cartcalc .ccalc-plus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            let b = +a+1;
            $(this).closest('.cartcalc').find('input').val(b);
        });
    }
    cartCalc()

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs-inner").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs-inner").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    // Making-order
    $('.making-order').click(function () {
        $('.order').addClass('order--active')
        $('body').addClass('fixed-body')
    })

    $('.order__close').click(function () {
        $('.order').removeClass('order--active')
        $('body').removeClass('fixed-body')
    })

    $(document).mouseup( function(e){
        var div = $( ".order__inner" );
        if ( !div.is(e.target)
            && div.has(e.target).length === 0 ) {
            div.parents('.order').removeClass('order--active');
            $('body').removeClass('fixed-body')
        }
    });

    // Basket
    $('.header__basket-show').click(function () {
        $('.basket').addClass('basket--active')
        $('body').addClass('fixed-body2')
    })

    $('.basket__close').click(function () {
        $('.basket').removeClass('basket--active')
        $('body').removeClass('fixed-body2')
    })

    $(document).mouseup( function(e){
        var div2 = $( ".basket__inner" );
        if ( !div2.is(e.target)
            && div2.has(e.target).length === 0 ) {
            div2.parents('.basket').removeClass('basket--active');
            $('body').removeClass('fixed-body2')
        }
    });

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})